import React, { useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import TopBar from "../../components/Users/TopBar";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Stepblock,
  SectionWrapper,
  Titletext,
  Wrapper,
} from "../../components/Styles/RegisterStyles";
import ModuleSideNav from "../../components/Users/ModuleSideNav";

import { Pie, Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  ArcElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import axios from "axios";
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  ArcElement,
  Title,
  Tooltip,
  Legend
);

export default function Dashboard() {
  const apiURLDashboard = "https://blueprintcatalyst.com/api/user/dashboard/";
  const storedUsername = sessionStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [CompanyData, setCompanyData] = useState("");
  const [CompanyDatashare, setCompanyDatashare] = useState("");
  const [Companyroundopen, setCompanyroundopen] = useState("");
  const [CompanyopenroundUserLog, setCompanyopenroundUserLog] = useState([]);
  const [investorreportLogs, setinvestorreportLogs] = useState([]);
  const [TotalInvestorscontact, setTotalInvestorscontact] = useState([]);
  const [recentuploadFile, setrecentuploadFile] = useState([]);
  const [investorDatarromreportLogs, setinvestorDatarromreportLogs] = useState(
    []
  );
  const [dilutionData, setDilutionforcastData] = useState({
    labels: [],
    datasets: [],
  });
  const [shareholderData, setShareholderData] = useState({
    labels: [],
    datasets: [
      {
        data: [],
        backgroundColor: [],
        borderWidth: 0,
      },
    ],
  });
  const [TotalInvestors, setTotalInvestors] = useState("");
  useEffect(() => {
    getCompanyTotalShares();
  }, []);
  useEffect(() => {
    getTotalinvestor();
    getTotalinvestorcontact();
    getinvestorreportLogs();
    getinvestorDatarromreportLogs();
  }, []);
  const getTotalinvestor = async (dataa) => {
    const formData = {
      user_id: userLogin.id,
    };

    try {
      const respo = await axios.post(
        apiURLDashboard + "getTotalinvestor",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setTotalInvestors(respo.data.results.length);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getTotalinvestorcontact = async (dataa) => {
    const formData = {
      user_id: userLogin.id,
    };

    try {
      const respo = await axios.post(
        apiURLDashboard + "getTotalinvestorcontact",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setTotalInvestorscontact(respo.data.results.length);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getinvestorreportLogs = async (dataa) => {
    const formData = {
      user_id: userLogin.id,
    };

    try {
      const respo = await axios.post(
        apiURLDashboard + "getinvestorreportLogs",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setinvestorreportLogs(respo.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getinvestorDatarromreportLogs = async (dataa) => {
    const formData = {
      user_id: userLogin.id,
    };

    try {
      const respo = await axios.post(
        apiURLDashboard + "getinvestorDatarromreportLogs",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setinvestorDatarromreportLogs(respo.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  useEffect(() => {
    getCompanystokes();
    getCompanyopenround();
    getCompanyopenroundUserLog();
    getDilutionForecast();
    getrecentuploadFile();
  }, []);
  const getrecentuploadFile = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const respo = await axios.post(
        apiURLDashboard + "getrecentuploadFile",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setrecentuploadFile(respo.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getShareholder = async (dataa) => {
    const formData = {
      user_id: userLogin.id,
      totalcompanyshare: dataa.company_shares,
    };

    try {
      const respo = await axios.post(
        apiURLDashboard + "getShareholder",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      // Map API response to Chart.js format
      console.log(respo.data);
      const data = {
        labels: respo.data.shareholders.labels,
        datasets: [
          {
            data: respo.data.shareholders.data,
            backgroundColor: respo.data.shareholders.colors,
            borderWidth: 0,
          },
        ],
      };

      setShareholderData(data);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };

  const getDilutionForecast = async () => {
    const formData = {
      user_id: userLogin.id,
      totalcompanyshare: CompanyData.company_shares,
    };
    try {
      const respo = await axios.post(
        apiURLDashboard + "getDilutionForecast",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      // console.log(respo.data);
      setDilutionforcastData(respo.data);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getCompanyTotalShares = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const respo = await axios.post(
        apiURLDashboard + "getCompanyTotalShares",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      setCompanyData(respo.data.results[0]);
      getShareholder(respo.data.results[0]);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getCompanystokes = async () => {
    const formData = {
      user_id: userLogin.id,
      Companyshares: CompanyData.company_shares,
    };
    try {
      const respo = await axios.post(
        apiURLDashboard + "getCompanystokes",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      if (respo.data.results.length > 0) {
        var dataa = respo.data.results[0];
        setCompanyDatashare(dataa);
      }
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getCompanyopenround = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const respo = await axios.post(
        apiURLDashboard + "getCompanyopenround",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      const openroundData = respo.data.roundInfo;
      setCompanyroundopen(openroundData);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const getCompanyopenroundUserLog = async () => {
    const formData = {
      user_id: userLogin.id,
    };
    try {
      const respo = await axios.post(
        apiURLDashboard + "getCompanyopenroundUserLog",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      const roundaccessLog = respo.data;
      if (roundaccessLog.length > 0) {
        setCompanyopenroundUserLog(roundaccessLog);
      }
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };

  const [dashboardData] = useState({
    equity: {
      totalShares: "10,000,000",
      optionPool: "15%",
      investorStakes: "62%",
      valuation: "$25M",
    },

    shareholders: {
      labels: [
        "Founders",
        "Series A Investors",
        "Series B Investors",
        "Option Pool",
        "Employees",
      ],
      data: [35, 25, 20, 15, 5],
      colors: [
        "#081828",
        "#092f4e",
        "#10395c",
        "#1a588d",
        "#2577bd",
        "#2577bd",
      ],
    },

    openRound: {
      type: "Series B",
      target: "$8M",
      raised: "$5.2M",
      preMoney: "$22M",
      closeDate: "Dec 15, 2023",
    },

    investors: {
      total: 24,
      contacts: 42,
      messages: [
        {
          name: "John Smith",
          firm: "VC Partners",
          message: "When will the next report be available?",
          time: "2h ago",
        },
        {
          name: "Sarah Johnson",
          firm: "Capital Growth",
          message: "Request for additional metrics...",
          time: "1d ago",
        },
      ],
    },

    dataRoom: {
      completion: 78,
      recentUploads: [
        {
          name: "Financials Q3 2023",
          description: "Updated projections",
          time: "Today",
        },
        {
          name: "Cap Table",
          description: "Latest revision",
          time: "Yesterday",
        },
      ],
    },

    accessLogs: [
      {
        name: "David Wilson",
        action: "Viewed Financial Reports",
        time: "Today, 4:00 PM",
      },
      {
        name: "Emily Chen",
        action: "Downloaded Cap Table",
        time: "Today, 5:00 PM",
      },
      {
        name: "Michael Brown",
        action: "Viewed Pitch Deck",
        time: "Yesterday, 10:00 AM",
      },
    ],
  });

  // Dilution forecast data
  // const dilutionData = {
  //   labels: ["Current", "After Series B", "After Series C"],
  //   datasets: [
  //     {
  //       label: "Founders",
  //       data: [35, 28, 22],
  //       backgroundColor: "#081828",
  //     },

  //     {
  //       label: "Series A",
  //       data: [25, 20, 16],
  //       backgroundColor: "#092f4e",
  //     },
  //     {
  //       label: "Series B",
  //       data: [0, 20, 16],
  //       backgroundColor: "#10395c",
  //     },
  //     {
  //       label: "Option Pool",
  //       data: [15, 15, 15],
  //       backgroundColor: "#1a588d",
  //     },
  //     {
  //       label: "Employees",
  //       data: [5, 5, 5],
  //       backgroundColor: "#2577bd",
  //     },
  //     {
  //       label: "Series C",
  //       data: [0, 0, 15],
  //       backgroundColor: "#2a85d3",
  //     },
  //   ],
  // };

  const dilutionOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Ownership Distribution Forecast",
      },
    },
    scales: {
      x: {
        stacked: true,
      },
      y: {
        stacked: true,
        max: 100,
        ticks: {
          callback: function (value) {
            return value + "%";
          },
        },
      },
    },
  };

  // const shareholderData = {
  //   labels: dashboardData.shareholders.labels,
  //   datasets: [
  //     {
  //       data: dashboardData.shareholders.data,
  //       backgroundColor: dashboardData.shareholders.colors,
  //       borderWidth: 0,
  //     },
  //   ],
  // };

  const shareholderOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: "bottom",
      },
    },
  };

  useEffect(() => {
    document.title = "Dashboard Page";
  }, []);
  const [isCollapsed, setIsCollapsed] = useState(false);

  function formatCurrentDateTime(input) {
    const date = new Date(input);

    if (isNaN(date)) return "";
    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();

    const getOrdinal = (n) => {
      if (n >= 11 && n <= 13) return "th";
      switch (n % 10) {
        case 1:
          return "st";
        case 2:
          return "nd";
        case 3:
          return "rd";
        default:
          return "th";
      }
    };

    return `${month} ${day}${getOrdinal(day)}, ${year}`;
  }
  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <div className="d-flex align-items-start gap-0">
            <ModuleSideNav
              isCollapsed={isCollapsed}
              setIsCollapsed={setIsCollapsed}
            />
            <div
              className={`global_view ${isCollapsed ? "global_view_col" : ""}`}
            >
              <TopBar />
              <SectionWrapper className="d-block p-4">
                <div className="container-fluid">
                  <Stepblock id="step5">
                    <div className="row">
                      <div className="col-md-12">
                        <div className="pb-3 bar_design">
                          <h4 className="h5 mb-0">Equity Snapshot</h4>
                        </div>

                        <div class="row gap-0 dashboard-top">
                          <div class="col-6 col-md-3 p-0 bor">
                            <div class="p-3">
                              <p class="small fw-medium mb-1">Total Shares</p>
                              <div className="d-flex align-items-center gap-3 justify-content-between">
                                <p class="h4 fw-semibold mb-0">
                                  $
                                  {Number(
                                    (
                                      CompanyData?.company_shares ?? "0"
                                    ).replace(/,/g, "")
                                  ).toLocaleString("en-US")}
                                </p>
                              </div>
                            </div>
                          </div>

                          <div class="col-6 col-md-3 p-0 bor">
                            <div class="p-3">
                              <p class="small fw-medium mb-1">Option Pool</p>
                              <p class="h4 fw-semibold mb-0">
                                {dashboardData.equity.optionPool}
                              </p>
                            </div>
                          </div>

                          <div class="col-6 col-md-3 p-0 bor">
                            <div class="p-3">
                              <p class="small fw-medium mb-1">
                                Investor Stakes
                              </p>
                              <p class="h4 fw-semibold mb-0">
                                {CompanyDatashare?.stake_percent ?? 0}%
                              </p>
                            </div>
                          </div>

                          <div class="col-6 col-md-3 p-0">
                            <div class="p-3">
                              <p class="small fw-medium mb-1">
                                Latest Valuation
                              </p>
                              <p class="h4 fw-semibold mb-0">
                                $
                                {Number(
                                  CompanyDatashare?.post_money_valuation ?? 0
                                ).toLocaleString("en-US")}
                              </p>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="col-12 my-4">
                        <div class="dashboard_card  modern-chart">
                          <div class="card-header">
                            <h3 class="card-title">Open Round Info</h3>
                          </div>

                          <div class="info-section">
                            <div class="info-item">
                              <div class="info-label">Round Type</div>

                              <div class="info-value">
                                {Companyroundopen?.round_type ?? ""}
                              </div>
                            </div>

                            <div class="info-item">
                              <div class="info-label">Target Raise</div>
                              <div class="info-value">
                                $
                                {Number(
                                  Companyroundopen?.target_raise ?? 0
                                ).toLocaleString("en-US")}
                              </div>
                            </div>

                            <div class="info-item">
                              <div class="info-label">Raised till Date</div>
                              <div class="info-value">
                                {" "}
                                {Companyroundopen.raised_to_date}
                              </div>
                            </div>

                            <div class="info-item">
                              <div class="info-label">Pre-money Valuation</div>
                              <div class="info-value">
                                {" "}
                                $
                                {Number(
                                  (CompanyData?.company_shares ?? "0").replace(
                                    /,/g,
                                    ""
                                  )
                                ).toLocaleString("en-US")}
                              </div>
                            </div>

                            <div class="info-item">
                              <div class="info-label">Expected Close</div>
                              <div class="info-value">
                                {" "}
                                {Companyroundopen.expected_close}
                              </div>
                            </div>
                          </div>

                          <div class="progress-container">
                            <div class="progress-info">
                              <div class="progress-label">
                                Fundraising Progress
                              </div>
                              <div class="progress-value">
                                {Companyroundopen.fundraising_progress ?? 0}
                              </div>
                            </div>
                            <div class="progress-bar">
                              <div
                                className="progress-fill"
                                style={{
                                  width: `${
                                    Companyroundopen?.progresswidth ?? 0
                                  }%`,
                                }}
                              ></div>
                            </div>
                          </div>

                          <div class="access-logs">
                            <h4 class="section-title">Access Logs</h4>

                            <table class="log-table">
                              <thead>
                                <tr>
                                  <th>User</th>
                                  <th>Action</th>
                                  <th>Time</th>
                                </tr>
                              </thead>
                              <tbody>
                                {CompanyopenroundUserLog?.length > 0 ? (
                                  CompanyopenroundUserLog.map((log, index) => (
                                    <tr key={index}>
                                      <td>
                                        <small>
                                          {log.first_name} {log.last_name}
                                        </small>
                                      </td>
                                      <td>
                                        <small>
                                          {log.nameOfRound} {log.shareClassType}{" "}
                                          {log.access_status}
                                        </small>
                                      </td>
                                      <td>
                                        <small>
                                          {formatCurrentDateTime(
                                            log.activity_date
                                          )}
                                        </small>
                                      </td>
                                    </tr>
                                  ))
                                ) : (
                                  <tr>
                                    <td
                                      colSpan="3"
                                      style={{ textAlign: "center" }}
                                    >
                                      No records found
                                    </td>
                                  </tr>
                                )}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>

                      <div className="mb-5 bar_design">
                        <h4 className="h5 mb-3">Dilution Forecast</h4>

                        <div className="barchart modern-chart">
                          <Bar data={dilutionData} options={dilutionOptions} />
                        </div>
                      </div>

                      <div class="dashboard-grid">
                        <div class="dashboard_card modern-chart">
                          <div class="card-header">
                            <h3 class="card-title">Shareholder Breakdown</h3>
                          </div>
                          <div className="h-100 d-flex justify-content-center align-items-center">
                            <div class="chart-container  mb-4">
                              <Pie
                                data={shareholderData}
                                options={shareholderOptions}
                              />
                            </div>
                          </div>
                        </div>

                        <div class="dashboard_card  modern-chart">
                          <div class="card-header">
                            <h3 class="card-title">Investor Reporting</h3>
                          </div>

                          <div class="stats-grid">
                            <div class="stat-card">
                              <div class="stat-label">Total Investors</div>
                              <div class="stat-value">{TotalInvestors}</div>
                            </div>
                            <div class="stat-card">
                              <div class="stat-label">Investor Contacts</div>
                              <div class="stat-value">
                                {TotalInvestorscontact}
                              </div>
                            </div>
                          </div>

                          <div class="messages-section">
                            <h4 class="section-title">
                              Messages From Investors
                            </h4>

                            <div class="message-item">
                              <div class="message-content">
                                <div class="message-header">
                                  <div class="message-sender">John Smith</div>
                                  <div class="message-time">2h ago</div>
                                </div>
                                <div class="message-firm">VC Partners</div>
                                <div class="message-text">
                                  When will the next report be available?
                                </div>
                              </div>
                            </div>

                            <div class="message-item">
                              <div class="message-content">
                                <div class="message-header">
                                  <div class="message-sender">
                                    Sarah Johnson
                                  </div>
                                  <div class="message-time">1d ago</div>
                                </div>
                                <div class="message-firm">Capital Growth</div>
                                <div class="message-text">
                                  Request for additional metrics...
                                </div>
                              </div>
                            </div>
                          </div>

                          <div class="access-logs">
                            <h4 class="section-title">Access Logs</h4>

                            <table class="log-table">
                              <thead>
                                <tr>
                                  <th>User</th>
                                  <th>Action</th>
                                  <th>Date Of View</th>
                                </tr>
                              </thead>
                              <tbody>
                                {investorreportLogs?.length > 0 ? (
                                  investorreportLogs.map((log, index) => (
                                    <tr key={index}>
                                      <td>
                                        <small>
                                          {log.first_name} {log.last_name}
                                        </small>
                                      </td>
                                      <td>
                                        <small>
                                          {log.document_name} (
                                          {log.access_status})
                                        </small>
                                      </td>
                                      <td>
                                        <small>
                                          {formatCurrentDateTime(log.date_view)}
                                        </small>
                                      </td>
                                    </tr>
                                  ))
                                ) : (
                                  <tr>
                                    <td
                                      colSpan="3"
                                      style={{ textAlign: "center" }}
                                    >
                                      No records found
                                    </td>
                                  </tr>
                                )}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>

                      <div className="col-12 mt-4">
                        <div class="dashboard_card  modern-chart">
                          <div class="card-header mb-5">
                            <h3 class="card-title">Data Room Status</h3>
                          </div>

                          <div class="progress-container">
                            <div class="progress-info">
                              <div class="progress-label">
                                Completion Status
                              </div>
                              <div class="progress-value">
                                {dashboardData.dataRoom.completion}%
                              </div>
                            </div>
                            <div class="progress-bar">
                              <div
                                class="progress-fill"
                                style={{
                                  width: `${dashboardData.dataRoom.completion}%`,
                                }}
                              ></div>
                            </div>
                          </div>
                          <div class="info-section">
                            <div class="info-item d-flex flex-column gap-2 w-100">
                              <h4 class="section-title w-100 text-start">
                                {" "}
                                Recent Uploads
                              </h4>
                              <table class="log-table">
                                <thead>
                                  <tr>
                                    <th>File Name</th>
                                    <th>Item Name</th>
                                    <th>Upload Date</th>
                                  </tr>
                                </thead>
                                <tbody>
                                  {recentuploadFile?.length > 0 ? (
                                    recentuploadFile.map((log, index) => (
                                      <tr key={index}>
                                        <td>
                                          <small>{log.doc_name}</small>
                                        </td>
                                        <td>
                                          <small>{log.name}</small>
                                        </td>
                                        <td>
                                          <small>
                                            {formatCurrentDateTime(
                                              log.created_at
                                            )}
                                          </small>
                                        </td>
                                      </tr>
                                    ))
                                  ) : (
                                    <tr>
                                      <td
                                        colSpan="3"
                                        style={{ textAlign: "center" }}
                                      >
                                        No records found
                                      </td>
                                    </tr>
                                  )}
                                </tbody>
                              </table>
                            </div>
                          </div>

                          <div class="access-logs">
                            <h4 class="section-title">Access Logs</h4>

                            <table class="log-table">
                              <thead>
                                <tr>
                                  <th>User</th>
                                  <th>Action</th>
                                  <th>Date Of View</th>
                                </tr>
                              </thead>
                              <tbody>
                                {investorDatarromreportLogs?.length > 0 ? (
                                  investorDatarromreportLogs.map(
                                    (log, index) => (
                                      <tr key={index}>
                                        <td>
                                          <small>
                                            {log.first_name} {log.last_name}
                                          </small>
                                        </td>
                                        <td>
                                          <small>
                                            {log.document_name} (
                                            {log.access_status})
                                          </small>
                                        </td>
                                        <td>
                                          <small>
                                            {formatCurrentDateTime(
                                              log.date_view
                                            )}
                                          </small>
                                        </td>
                                      </tr>
                                    )
                                  )
                                ) : (
                                  <tr>
                                    <td
                                      colSpan="3"
                                      style={{ textAlign: "center" }}
                                    >
                                      No records found
                                    </td>
                                  </tr>
                                )}
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                    </div>
                  </Stepblock>
                </div>
              </SectionWrapper>
            </div>
          </div>
        </div>
      </Wrapper>
    </>
  );
}
